import "core-js/modules/es.array.push.js";
import router from '@/router';
import { ref } from 'vue';
import { Toast } from 'vant';
export default {
  setup() {
    let back = () => {
      router.push({
        path: "/my"
      });
    };
    const showShare = ref(false);
    const options = [{
      name: '微信',
      icon: 'wechat'
    }, {
      name: '微博',
      icon: 'weibo'
    }, {
      name: '复制链接',
      icon: 'link'
    }, {
      name: '分享海报',
      icon: 'poster'
    }, {
      name: '二维码',
      icon: 'qrcode'
    }];
    const onSelect = option => {
      Toast(option.name);
      showShare.value = false;
    };
    return {
      back,
      options,
      onSelect,
      showShare
    };
  }
};